/**
 * ----------------------------------------------------------------------------
 *  BASE
 * ----------------------------------------------------------------------------
 */

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
}

main {
  flex: 1;
}

input {
  font-family: Roboto, sans-serif;
}

.page__content {
  margin-left: auto;
  margin-right: auto;
}